export default "<template>\r\n    <div>\r\n        <raw-loader :mytxt=\"require('!raw-loader!./Tpl2.vue').default\"></raw-loader>\r\n        <h1 class=\"border-bottom\">KonvaTpl2</h1>\r\n        {{ msg }}<br>\r\n        {{ selId }}<br>\r\n        <b-row>\r\n            <b-col lg=\"4\">\r\n                <button class=\"mb-1\" @click=\"onClearSelect\">Clear Select</button>\r\n                <div id=\"stage-parent\">\r\n                    <div id=\"container\" class=\"border\">\r\n                        <v-stage ref=\"stage\" @click=\"onStageClick\" @mousemove=\"onStageMouseMove\"\r\n                            @mouseenter=\"onStageMouseenter\" @mouseleave=\"onStageMouseleave\" :config=\"configKonva\">\r\n                            <v-layer ref=\"layer\">\r\n                                <v-rect v-for=\"(x, k) in items\" :key=\"k\" :config=\"x\" @dragstart=\"handleDragstart\"\r\n                                    @dragend=\"handleDragend\" />\r\n                                <v-circle ref=\"circle\"></v-circle> \r\n                                <v-rect ref=\"rect\"></v-rect>\r\n                                <v-transformer ref=\"transformer\" />\r\n                            </v-layer>\r\n                        </v-stage>\r\n                    </div>\r\n                </div>\r\n                <button class=\"mt-1\" @click=\"onSave\">Save</button>\r\n            </b-col>\r\n            <b-col lg=\"8\">\r\n                right\r\n            </b-col>\r\n        </b-row>\r\n    </div>\r\n</template>\r\n<script>\r\nvar sceneWidth = 1500;\r\nvar sceneHeight = 1000;\r\nvar stage = null;\r\nvar layer = null;\r\nfunction generateItems() {\r\n    let w=200;\r\n    let h=200;\r\n    let width = sceneWidth - w;\r\n    let height = sceneHeight - h;\r\n    const items = [];\r\n    for (let i = 0; i < 10; i++) {\r\n        items.push({\r\n            x: Math.random() * width,\r\n            y: Math.random() * height,\r\n            radius: 50,\r\n            id: \"node-\" + i,\r\n            width: w,\r\n            height: h,\r\n            fill: Konva.Util.getRandomColor(),\r\n            stroke: 'black', //Konva.Util.getRandomColor(),\r\n            draggable: true,\r\n            strokeWidth: 4,\r\n        });\r\n    }\r\n    return items;\r\n}\r\nexport default {\r\n    data() {\r\n        return {\r\n            configKonva: {\r\n                width: sceneWidth,\r\n                height: sceneHeight\r\n            },\r\n            items: [],\r\n            msg: '',\r\n            count:0,\r\n            selId:-1,\r\n        }\r\n    },\r\n    mounted() {\r\n        //this.items = generateItems();\r\n        this.$nextTick(function () {\r\n            layer = this.$refs.layer.getNode();\r\n            stage = this.$refs.stage.getNode();\r\n            window.addEventListener(\"resize\", this.fitStageIntoParentContainer);\r\n            this.fitStageIntoParentContainer();\r\n        })\r\n        //this.transformerNode = this.$refs.transformer.getNode();\r\n    },\r\n    watch: {\r\n        selId: function(newValue,oldValue) {\r\n            let ofill=this.getById(this.selId).fill();\r\n            this.getById(this.selId).setAttr(ofill);\r\n            this.getById(this.selId).fill(\"yellow\");\r\n            this.clearTransformer();\r\n            this.addTransformer(this.getById(this.selId));\r\n            if(oldValue==-1){\r\n            }else{\r\n                this.getById(oldValue).fill(this.getById(oldValue).getAttr('ofill'));\r\n            }\r\n        }\r\n    },\r\n    methods: {\r\n        clearTransformer(){\r\n            let stage = this.$refs.stage.getNode();\r\n            let trs=stage.find('.rectTransformer');\r\n            trs.forEach(x=>{\r\n                x.destroy();\r\n            })\r\n        },\r\n        addTransformer(shape){\r\n            let stage = this.$refs.stage.getNode();\r\n            const transformerNode = this.$refs.transformer.getNode().clone();\r\n            transformerNode.name('rectTransformer');\r\n            let layer=this.$refs.layer.getNode();\r\n            layer.add(transformerNode);\r\n            transformerNode.nodes([shape]);\r\n        },\r\n        onClearSelect(){\r\n            this.clearTransformer();\r\n        },        \r\n        onSave() {\r\n            let stage = this.$refs.stage.getNode();\r\n            var dataURL = stage.toDataURL({ pixelRatio: 0.5 });\r\n            alert(dataURL);\r\n        },\r\n        handleDragstart(e) {\r\n            e.target.moveToTop();\r\n        },\r\n        handleDragend(e) {\r\n            e.target.fill('yellow');\r\n        },\r\n        onStageClick(e) {\r\n            let p = this.myGetMouse();\r\n            let layer=this.$refs.layer.getNode();\r\n\r\n            if(e.target.id()==''){\r\n                //let circle=this.makeCircle(this.getCount(),p.x,p.y,70);\r\n                let circle=this.makeRect(this.getCount(),p.x,p.y,100,100);\r\n                layer.add(circle);\r\n            }else{\r\n                e.target.setAttr('ofill',e.target.fill());\r\n                this.selId=e.target.id();\r\n            }\r\n        },\r\n        onStageMouseMove(e) {\r\n            let p = this.myGetMouse();\r\n        },\r\n        onStageMouseenter(e) {\r\n        },\r\n        onStageMouseleave(e) {\r\n        },\r\n        getCount(){\r\n            let kq=this.count;\r\n            this.count++;\r\n            return kq;\r\n        },\r\n        makeCircle(id,x,y,radius){\r\n            let circle=this.$refs.circle.getNode().clone();\r\n            circle.id(id.toString());\r\n            circle.x(x);\r\n            circle.y(y);\r\n            circle.radius(radius);\r\n            circle.draggable(true);\r\n            circle.fill(Konva.Util.getRandomColor());\r\n            circle.stroke('blue');\r\n            return circle;\r\n        },\r\n        makeRect(id,x,y,w,h){\r\n            let circle=this.$refs.rect.getNode().clone();\r\n            circle.id(id.toString());\r\n            circle.x(x);\r\n            circle.y(y);\r\n            circle.width(w);\r\n            circle.height(h);\r\n            circle.draggable(true);\r\n            circle.fill(Konva.Util.getRandomColor());\r\n            //circle.stroke('blue');\r\n            return circle;\r\n        },\r\n        myGetMouse() {\r\n            let scale = stage.scale().x;\r\n            var mousePos = stage.getPointerPosition();\r\n            var x = mousePos.x;\r\n            var y = mousePos.y;\r\n            var xx = Math.round(x / scale);\r\n            var yy = Math.round(y / scale);\r\n            return { x: xx, y: yy };\r\n        },\r\n        fitStageIntoParentContainer() {\r\n            var container = document.querySelector('#stage-parent');\r\n            var containerWidth = container.offsetWidth;\r\n            var scale = containerWidth / sceneWidth;\r\n            stage.width(sceneWidth * scale);\r\n            stage.height(sceneHeight * scale);\r\n            stage.scale({ x: scale, y: scale });\r\n            this.configKonva.width = sceneWidth * scale;\r\n            this.configKonva.height = sceneHeight * scale;\r\n        },\r\n        getById(id){\r\n            let layer=this.$refs.layer.getNode(); \r\n            return layer.find(`#${id}`)[0] \r\n        },\r\n    }\r\n}\r\n</script>\r\n  ";